<script>
	export default {
		data() {
			return {
				bianji: false,
				xinwen_id:"",
				list:{}
			}
		},
		methods: {
			liuyan_kai: function() {
				this.bianji = true
			},
			liuyan_guan: function() {
				this.bianji = false
			},
			huoqu_xinwen:function(){
				let _this =  this
				let data = {
					quartersId:getApp().globalData.public_user_property_id,
					noticeId:_this.xinwen_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/getnoticedetails', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list = value
					}
				})
				
			}
		},
		onLoad:function(res){
			this.xinwen_id = res.id 
			this.huoqu_xinwen()
			// console.log(res.id)
		}
	}
</script>

